import { Injectable } from '@angular/core';
import { ApiService, CoreService } from '@core/index';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/observable/forkJoin';

@Injectable({
  providedIn: 'root'
})
export class DiaryService {

  constructor(
    private apiService: ApiService,
    private coreService: CoreService,
  ) { }

  getAll(params?) {
    let includes = 'includes[]=user';
    let filter = ""
    if(params){
      filter = '&' + params;
    }
    return this.apiService.getDataTable("/logs", includes + filter);
  }

  getListByListtypeByCode(codes){
    return this.apiService.get("/get-lists-by-listtype-code/" + codes);
  }
  /* Báo cáo biến động người dùng */
  reportBienDongNguoiDung(data) {
    return this.apiService.post("/logs/user_report", data);
  }
  /* Báo cáo biến động nhóm quyền */
  reportBienDongNhomQuyen(data) {
    return this.apiService.post("/logs/role_report", data);
  }

  getCatLogs(){
    let actionLogs = this.apiService.get("/get-lists-by-listtype-code/DM_HANH_DONG_LOG");
    let functionLogs = this.apiService.get("/get-lists-by-listtype-code/DM_CHUC_NANG_LOG");
    return Observable.forkJoin([actionLogs, functionLogs]);
  }
}
