import { Component, OnInit, ViewChild } from '@angular/core';

import { DiaryService } from '@modules/diary/shared';
import { ActivatedRoute, Router } from '@angular/router';
import { Helper } from '@core/helper/Helper';
import { DxDataGridComponent } from 'devextreme-angular';
import * as moment from 'moment';
import { AuthService, Library } from '@core/index';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.scss']
})
export class IndexComponent implements OnInit {
  @ViewChild(DxDataGridComponent) dataGrid: DxDataGridComponent;

  popupDetailVisible = false;
  rowDetail: any;
  dataSource: any = {};
  startDate: any;
  endDate: any;
  noDataText: any;
  selectedRows: any;
  actionLogs: any;
  functionLogs: any;
  exportSchedules: any;
  _translate: any;
  exportSchedulesDefault: any;
  extras = [];
  constructor(
    private authService: AuthService,
    private diaryService: DiaryService,
    private route: ActivatedRoute,
    public translate: TranslateService,

  ) {
    this._translate = this.authService.getTranslate();
    translate.use(this.authService.getLanguage());
    this.extras = [{
      id: '1',
      name: this._translate.CONTROLL.BUTTON.export_file,
      items: [
        {
          id: 'DU_LIEU_NHAT_KY',
          name: this._translate.CONTROLL.BUTTON.log_data,
          icon: this._translate.ICONS.excel
        },
        {
          id: 'BC_BIEN_DONG_NGUOI_DUNG',
          name: this._translate.CONTROLL.BUTTON.user_volatility_report,
          icon: this._translate.ICONS.excel
        },
        {
          id: 'BC_BIEN_DONG_NHOM_QUYEN',
          name: this._translate.CONTROLL.BUTTON.group_change_report,
          icon: this._translate.ICONS.excel
        }
      ]
    }];
  }

  ngOnInit() {
    this.route.data.subscribe((res: any) => {
      if (res.catLogs[0].status == "success") {
        this.actionLogs = res.catLogs[0].data.DM_HANH_DONG_LOG;
      }
      if (res.catLogs[1].status == "success") {
        this.functionLogs = res.catLogs[1].data.DM_CHUC_NANG_LOG;
      }
    });

    this.exportSchedules = Helper.getExportSchedule(this._translate, true).schedule;
    console.log(this.exportSchedules,'this.exportSchedules');
    
    this.exportSchedules = this.exportSchedules.filter(p => p.code != 'TAT_CA');
    let _obj = this.exportSchedules.filter(p => p.code == "TUAN_NAY")[0];
    if (_obj) {
      this.exportSchedulesDefault = _obj.code;
      this.startDate = _obj.startDate;
      this.endDate = _obj.endDate;
      this.loadListByFilter();
    }

  }

  loadList() {
    this.dataSource.store = this.diaryService.getAll();
  }

  loadListByFilter() {
    let params = `filter_groups[0][filters][0][key]=created_at&filter_groups[0][filters][0][value][0]=${this.startDate}&filter_groups[0][filters][0][value][1]=${this.endDate}&filter_groups[0][filters][0][operator]=bt`
    this.dataSource.store = this.diaryService.getAll(params);
  }

  showDetail(dataRow) {
    this.popupDetailVisible = true;
    this.rowDetail = dataRow.data;
    this.actionLogs.forEach(actionLog => {
      if (actionLog.code == dataRow.data.action_code) {
        this.rowDetail.action_name = actionLog.name;
      }
    });
    this.functionLogs.forEach(functionLog => {
      if (functionLog.code == dataRow.data.function_code) {
        this.rowDetail.function_name = functionLog.name;
      }
    });
    this.rowDetail.created_at_view = moment(dataRow.data.created_at).format('DD-MM-YYYY HH:mm:ss');
  }
  onExportSchedules = (e) => {
    let selectedItem = e.component.option("selectedItem");
    this.startDate = selectedItem.startDate;
    this.endDate = selectedItem.endDate;
    this.dataSource = {};
    this.loadListByFilter();
  }
  onStartDate(e) {
    if (e.event) {
      let startDate = moment(e.value).format('YYYY-MM-DD HH:mm:ss');
      this.startDate = startDate;
      this.dataSource = {};
      this.loadListByFilter();
    }
  }
  onEndDate(e) {
    if (e.event) {
      let endDate = moment(e.value).format('YYYY-MM-DD HH:mm:ss');
      this.endDate = endDate;
      this.dataSource = {};
      this.loadListByFilter();
    }
  }

  /* Dữ liệu nhật ký */
  dulieunhatky() {
    this.dataGrid.instance.exportToExcel(false);
  }
  /* Báo cáo biến động người dùng */
  baocaobiendongND() {
   let data = {
      'log': {
        'from_date': this.startDate.split(" ", 1)[0],
        'to_date': this.endDate.split(" ", 1)[0],
      }
    };
    Library.showloading();
    this.diaryService.reportBienDongNguoiDung(data).subscribe(res => {
      if (res.status === 'success') {
        Helper.dowloadFile(res.data.log.file_name, 'xlsx', res.data.log.file_content);
        Library.notify('Xuất file Excel thành công', 'success');
      } else {
        Library.notify(res.message, 'error');
      }
      Library.hideloading();
    });
  }
  /* Báo cáo biến động nhóm quyền */
  baocaobiendongNQ() {
    let data = {
      'log': {
        'from_date': this.startDate.split(" ", 1)[0],
        'to_date': this.endDate.split(" ", 1)[0],
      }
    };
    Library.showloading();
    this.diaryService.reportBienDongNhomQuyen(data).subscribe(res => {
      if (res.status === 'success') {
        Helper.dowloadFile(res.data.log.file_name, 'xlsx', res.data.log.file_content);
        Library.notify('Xuất file Excel thành công', 'success');
      } else {
        Library.notify(res.message, 'error');
      }
      Library.hideloading();
    });
  }
  /* Chức năng xuất file log */
  itemExtrasClick(e) {
    if (e.itemData.id === 'DU_LIEU_NHAT_KY') {
      this.dulieunhatky();
    } else if (e.itemData.id === 'BC_BIEN_DONG_NGUOI_DUNG') {
      this.baocaobiendongND();
    } else if (e.itemData.id === 'BC_BIEN_DONG_NHOM_QUYEN') {
      this.baocaobiendongNQ();
    }
  }
}
